                var r = window.location.search.substr(1).match(reg);  //å¹éç®æ åæ°
                if (r != null) return unescape(r[2]);
                return null; //è¿ååæ°å¼
            },
            async checkNet() {
                let _this = this;

                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
