                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 42) {   //ä»¥å¤ªåæµè¯ç½  K
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
