                            return false;
                        }
                        else if (networkVersion == 56) { //å¸å®ä¸»ç½
                            _this.type = 'BNB';
                        }
                        else if (networkVersion == 97) { //å¸å®æµè¯ç½
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        conContract = new ethers.Contract(_this.api.conAddress,conAbi, signer);
